import React, { Component } from 'react';
import '../styles/Header.scss';
import logo from '../images/logo.png';
import logo2 from '../images/logo2.png';

class Header extends Component {
  render() {
    return (
      <header className="header">
        <div className="header-content">
          <div className="options">
            <img src={logo} className="logoBanner" alt="logo" />
            <h2>Veja os pets disponíveis</h2>
            <p>
              Utilize agora nossa plataforma para doar ou adotar um pet. Utilize agora nossa plataforma para doar ou adotar um pet.
              Utilize agora nossa plataforma para doar ou adotar um pet.
            </p>
            <div className="botoes">
              <a id="contact-button" className="btn-primary" href="/AnimaisDisponiveis">
                Adotar um animal
              </a>
              <br />
              <a href="https://api-4patas.onrender.com/AdicionarAnimal" id="contact-button" className="btn-secondary">
                Doar um animal
              </a>
            </div>
          </div>
          <div className="options">
            <img src={logo2} className="logoBanner" alt="logo" />
          </div>
        </div>
      </header>
    );
  }
}

export default Header;
